<template>
  <v-container>
    <v-row align-content="center" justify="center">
      <v-col
        cols="12"
        sm="6"
        md="2"
      >
        <v-select
          :items="SET"
          v-model="equipment.set"
          dense
          solo
        >
          <template slot="label">
            
          </template>
          <template v-slot:selection="{ item }">
            <span class="d-flex justify-center" style="width: 100%;">
              {{ $t(item.text) }}
            </span>
          </template>
          <template v-slot:item="{ item }">
            <span class="d-flex justify-center" style="width: 100%;">
              {{ $t(item.text) }}
            </span>
          </template>
        </v-select>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="2"
      >
        <v-text-field
          label="(a)"
          v-model.lazy="equipment.atk"
          hide-details
          single-line
          type="number"
        ></v-text-field>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="2"
      >
        <v-text-field
          label="%(a)"
          v-model.lazy="equipment.atkp"
          hide-details
          single-line
          type="number"
        ></v-text-field>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="2"
      >
        <v-text-field
          label="(c)"
          v-model.lazy="equipment.cri"
          hide-details
          single-line
          type="number"
        ></v-text-field>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="2"
      >
        <v-text-field
          label="(x)"
          v-model.lazy="equipment.crid"
          hide-details
          single-line
          type="number"
        ></v-text-field>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="2"
      >
        <v-text-field
          label="(s)"
          v-model.lazy="equipment.spd"
          hide-details
          single-line
          type="number"
        ></v-text-field>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="2"
      >
        <v-text-field
          label="(h)"
          v-model.lazy="equipment.hp"
          hide-details
          single-line
          type="number"
        ></v-text-field>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="2"
      >
        <v-text-field
          label="%(hp)"
          v-model.lazy="equipment.hpp"
          hide-details
          single-line
          type="number"
        ></v-text-field>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="2"
      >
        <v-text-field
          label="(d)"
          v-model.lazy="equipment.def"
          hide-details
          single-line
          type="number"
        ></v-text-field>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="2"
      >
        <v-text-field
          label="%(d)"
          v-model.lazy="equipment.defp"
          hide-details
          single-line
          type="number"
        ></v-text-field>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="1"
      >
        <v-text-field
          label="%(t)"
          v-model.lazy="equipment.shit"
          hide-details
          single-line
          type="number"
        ></v-text-field>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="1"
      >
        <v-text-field
          label="%(r)"
          v-model.lazy="equipment.sres"
          hide-details
          single-line
          type="number"
        ></v-text-field>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="2"
      >
        <v-btn @click="add" width="100%">Add</v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        sm="12"
      >
        <v-text-field
          v-model="fast"
          label="(:a10 a10.9 c10.9 x20.1)=(10 10.9 10.9 20.1)  %"
          hint="a/% c x s h/% d/% t r"
          @change="tryParse"
          filled
        ></v-text-field>
      </v-col>
    </v-row>
    <EquipmentList :equipments="equipments" />
  </v-container>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex';
import EquipmentList from '@/components/EquipmentList.vue';

function isInt(n) {
  return n % 1 === 0;
}

function isFloat(n){
    return Number(n) === n && n % 1 !== 0;
}

export default {
  name: 'CalculatorContainer',
  components: {
    EquipmentList,
  },
  data() {
    return {
      SET: [
        { text: '', value: 0},
        { text: '', value: 1},
        { text: '', value: 2},
        { text: '', value: 3},
      ],
      SHORT_GUESS: {
        a: 'atk',
        af: 'atkp',
        cf: 'cri',
        xf: 'crid',
        cdf: 'crid',
        s: 'spd',
        h: 'hp',
        hf: 'hpp',
        d: 'def',
        df: 'defp',
        t: 'shit',
        r: 'res',
        tf: 'shit',
        rf: 'sres',
      },
      GUESS: {
        a: 'a',
        ap: 'atkp',
        c: 'cri',
        cd: 'crid',
        s: 'spd',
        h: 'hp',
        hp: 'hpp',
        d: 'def',
        dp: 'defp',
        sh: 'shit',
        sr: 'sres',
      },
      FAST: {
        a: 'atk',
        b: 'atkp',
        c: 'cri',
        d: 'crid',
        e: 'spd',
        f: 'hp',
        g: 'hpp',
        h: 'def',
        i: 'defp',
        j: 'shit',
        k: 'sres',
      },
      drawer: false,
      forceRefresed: false,
      copied: false,
      name: '',
      fast: '',
      equipment: {
        set: 0,
        atk: '',
        atkp: '',
        def: '',
        defp: '',
        hp: '',
        hpp: '',
        spd: '',
        shit: '',
        sres: '',
        cri: '',
        crid: '',
      },
    };
  },
  computed: {
    ...mapState(['equipments']),
  },
  methods: {
    ...mapActions(['fetchData']),
    ...mapMutations(['save']),
    tryParse() {
      if (this.fast.trim() === '') {
        return;
      }
      const a = this.fast.split(' ');
      a.forEach((i) => {
        const result = new RegExp(/([A-Za-z]+)([0-9]*[.]*[0-9]*)/).exec(i);
        if (!result) {
          return;
        }
        // const appendix = result[2].includes('.') ? 'f' : '';
        // if the value is less than 30, we treat it as floating(percentage)
        const appendix = Number(result[2]) < 30 ? 'f' : '';
        if (this.SHORT_GUESS[`${result[1]}${appendix}`] in this.equipment) {
          this.equipment[this.SHORT_GUESS[`${result[1]}${appendix}`]] = result[2];
        } else if (this.GUESS[result[1]] in this.equipment) {
          this.equipment[this.GUESS[result[1]]] = result[2];
        } else if (this.FAST[result[1]] in this.equipment) {
          this.equipment[this.FAST[result[1]]] = result[2];
        }
      });
      this.fast = '';
      this.add();
    },
    add() {
      this.save(this.equipment);
      this.$set(this, 'equipment', {
        set: 0,
        atk: '',
        atkp: '',
        def: '',
        defp: '',
        hp: '',
        hpp: '',
        spd: '',
        shit: '',
        sres: '',
        cri: '',
        crid: '',
      });
    },
  },
  mounted() {
    window.appc = this;
    this.fetchData();
  },
};
</script>

